    .gform_wrapper {
        margin:0px;
    }
    .gfield_error .ginput_container input, .gfield_error .ginput_container select {
        border: 1px solid #ff0000;
}
    .gfield {

        position:relative;
    }
    .gfield .gfield_label {
        position:absolute;
        top:0px;
        left:0px;
        text-transform:uppercase;
        font-size:10px;
        font-weight:600;
        margin:5px;
        font-family:"Open Sans";
}
    .gfield .ginput_container {
        margin-bottom:10px;
}
    .gfield .ginput_container input {
        width:100%;
        height:100%;
        padding:20px 5px 5px;
}
    .gfield .ginput_container input::-webkit-input-placeholder {
        letter-spacing:3px;
}
    .gfield .ginput_container input:-moz-placeholder {
        /* older Firefox*/
        letter-spacing:3px;
}
    .gfield .ginput_container input::-moz-placeholder {
        /* Firefox 19+ */
        letter-spacing:3px;
}
    .gfield .ginput_container input:-ms-input-placeholder {
        letter-spacing:3px;
}
    .gfield .ginput_container .gfield_checkbox, .gfield .ginput_container .gfield_radio {
        clear:both;
        *zoom:1;
}
    .gfield .ginput_container .gfield_checkbox:before, .gfield .ginput_container .gfield_radio:before {
        content:'';
        display:table;
}
    .gfield .ginput_container .gfield_checkbox:after, .gfield .ginput_container .gfield_radio:after {
        content:'';
        display:table;
        clear:both;
}
    .gfield .ginput_container .gfield_checkbox:before, .gfield .ginput_container .gfield_checkbox:after, .gfield .ginput_container .gfield_radio:before, .gfield .ginput_container .gfield_radio:after {
        content:" ";
        clear:both;
        display:block;
}
    .gfield .ginput_container .gfield_checkbox label, .gfield .ginput_container .gfield_radio label {}
    .gfield .ginput_container .gfield_checkbox li, .gfield .ginput_container .gfield_radio li {
        float:left;
        margin-right:10px;
}
    .gfield .ginput_container .gfield_checkbox input, .gfield .ginput_container .gfield_radio input {
        display:none;
}
    .gfield.purple {
        background:$purple;
}
    .gfield.purple .ginput_container_select:after{
        border-top-color:$white !important;
}
    .gfield.purple .ginput_container_select select{
        color:$white;
}
    .gfield.purple .ginput_container_select select option {
        background:$purple;
}
    .gfield .ginput_container_select select {
        position:relative;
        -webkit-appearance:none;
           -moz-appearance:none;
                appearance:none;
        width:100%;
        height:100%;
        padding-right:50px;
        background:none;
        z-index:1;
}
    .gfield .ginput_container_select:after {
        position:absolute;
        top:50%;
        right:10px;
        display:block;
        content:" ";
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top:8px solid $black;
        margin-top:-4px;
}
    .gfield .ginput_container_select:before {
        position:absolute;
        top:0;
        right:0;
        display:block;
        content:" ";
        border:1px solid $gray;
        width:40px;
        height:100%;
}
    input[type=checkbox] + label, input[type=radio] + label {
        font-family:'Open Sans';
        color:$black;
    }
    .disabled + label:before {
        border:solid 1px #dcdcdc !important;

    }
    .disabled:checked + label {
        color:#dcdcdc;
    }
    .disabled:checked + label:before {
        background:#dcdcdc !important;

    }
    input[type=checkbox] { display:none; }
    /* to hide the checkbox itself */
    input[type=checkbox] + label:before {
        display: inline-block;
        font-family: 'icomoon';
        color:$white;
        font-size:12px;


    }
    input[type=checkbox] + label:before {
        margin-right:5px;
        content:'\e914';
        text-align:center;
        padding-top:1px;
        width:15px;
        height:15px;
        border:solid 1px $gray;
    }
    /* unchecked icon */
    .gfield_radio li label {
        display:-webkit-box;
        display:-webkit-flex;
        display:-ms-flexbox;
        display:flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
            -ms-flex-pack: center;
                justify-content: center;
    }
    input[type=radio] + label:before {
        margin-right:5px;
        content:' ';
        text-align:center;
        width:5px;
        height:5px;
        display:inline-block;
        border-radius:50%;
        border:1px solid $gray_dark1;
        padding:10px;
        box-shadow:inset 0px 0px 0 7px $white;
    }
    /* unchecked icon */
    input[type=radio]:disabled + label:before {
        border-color:$gray_medium1;
    }
    input[type=radio]:checked:disabled + label:before {
        background:$gray_medium1;
        border-color:$gray_medium1;
        box-shadow:inset 0px 0px 0 7px $white;
    }
    input[type=radio]:checked + label:before {

        background:$blue_light;
        border-color:$blue_light;
    }
    /* checked icon */
    input[type=checkbox]:checked + label:before {
        content:'\e914';
        background:$blue_light;
        border-color:$blue_light;
    }
    /* checked icon */
    .ginput_container input, .ginput_container select {
        font-size:16px;
        line-height:28px;
        padding:.313em 0;
        border:1px solid $gray;
}
    .ginput_container select {
        padding:9px;
}
    @media (min-width: $(desktop)) {
        .gform_wrapper {
                margin:20px;
        }
}
    .gform_body {
    clear:both;
    *zoom:1;
}
    .gform_body:before {
        content:'';
        display:table;
}
    .gform_body:after {
        content:'';
        display:table;
        clear:both;
}
    .gform_body:before, .gform_body:after {
        content:" ";
        clear:both;
        display:block;
}
    .gform_footer {
    margin:20px 0;
}
    .gfield_label {
    font-weight:bold;
    margin:.625em 0 .5em;
    clear:both;
    *zoom:1;

    display:block;
}
    .gfield_label:before {
        content:'';
        display:table;
}
    .gfield_label:after {
        content:'';
        display:table;
        clear:both;
}
    .gfield_label:before, .gfield_label:after {
        content:" ";
        clear:both;
        display:block;
}
    .gfield_required {
    color:#790000;
    margin-left:2px;
    display:inline-block;
}